
@import url(./assets/external-css/bootstrap.css);
@import url(./assets/external-css/bootstrap-vue.css);
.defaultHeight {
  min-height: 100vh;
  max-height: 100%;
  height: 100%;
}
h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.header {
  height: 75px;
  background-color: #eb7475;
  line-height: 75px;
  padding: 0rem 1rem;
}
.header h1 {
    font-weight: 300;
}
.header h2 {
    text-align: right;
    font-size: 12px;
    text-transform: uppercase;
}



